import React from "react";
import { FavoriteButton } from "../buttons/favorite/favorite.component";
import { IconClose } from "../icons/close.icon";
import { IPostProps } from "./post.model";
import "./post.scss";

export const Post = ({id,preview, title, cat ,onToggleLike, like, onRemove}: IPostProps) =>{
    const onChangeLike = ()=>{
        onToggleLike(id)
    }
    const onRemovePost = ()=>{
        onRemove(id);
    }
    return(
        <div className="post post_padding-navigation">
            <div className="post__preview-container">
                <img src={preview} alt="" className="post__preview"/>
                <div className="post__cat post_cat-position-absolute">{cat}</div>
                <button className="post-remove post__remove" onClick={onRemovePost}>
                    <IconClose className="post-remove__icon"/>
                </button>
            </div>
            <h3 className="post__title">
                {title}
            </h3>
            <div className="post__navigation post-navigation">
                <a href={preview} target="_blank" rel="noreferrer" className="post__link">
                        Link
                </a>
                <div className="post__like">
                    <FavoriteButton onClick={onChangeLike} active={like}/>
                </div>
            </div>

        </div>
    )
}