import { IQuery } from "../services/api";

export const parseQuery = (query?: IQuery): string=>{
    let q = '';
    const queryKeys = Object.keys(query || {});
    if(query && queryKeys.length > 0){
        q+="?";
        queryKeys.forEach((item, index)=>{
            if(queryKeys.length !== index){
                q+=`${item}=${query[item]}&`
            } else {
                q+=`${item}=${query[item]}`
            }
        })
    }

    return q
}