import React from "react";
import { Container } from "../../components/container/container.component";
import { Post } from "../../components/post/post.component";
import { IImagesData } from "../../store/slices/images/images.model";
import { IHomePageProps } from "./home.model";
import { TransitionGroup, CSSTransition} from 'react-transition-group';
import './home.scss'

export const HomePage = ({error, data, onChangeLike, onRemoveImage, onFilter, filter}: IHomePageProps) =>  {
    const isEmpty = data.length === 0 && error === '';
    const onChangeFilter = <TValue extends unknown>(key: string, value: TValue)=>{
        onFilter({...filter,[key]: value})
    }
    return(
        <div className="home">
            <Container>
                <>
                    <h1 className="home__title">Images:</h1>

                   { 
                        <div className="home__filter">
                            <button 
                                className={`btn ${filter.like && filter.like === true ? "home_filter-active" : ""}`}
                                onClick={()=> onChangeFilter<boolean | undefined>("like", !filter.like)}
                            >
                                    Filter like
                                </button>
                        </div>
                    }

                    <TransitionGroup className={"home__list home-post"}>
                            {
                                error !== '' ? error :

                                data.map((item: IImagesData)=>(
                                    <CSSTransition key={item.id} timeout={700} classNames="del">
                                        <div className="home-post__item " >
                                            <Post
                                                id={item.id} 
                                                preview={item.url}
                                                like={item?.like || false}
                                                title={"A picture with a cat can be opened: "}
                                                cat={item.categories && item.categories[0].name ? item.categories[0].name : "All"}
                                                onToggleLike={onChangeLike}
                                                onRemove={onRemoveImage}
                                            />
                                        </div>
                                    </CSSTransition>
                                ))
                            }
                    </TransitionGroup>
                    { isEmpty && <div className="home__empty">Data empty !</div>}

                </>
            </Container>

        </div>
    )
}