import { catchError, from,  Observable, of,  switchMap } from "rxjs";
import { IImagesData } from "../store/slices/images/images.model";
import { parseQuery } from "../utils/api.utils";

export interface IResponse<TMeta, TError, TData> {
    meta?: TMeta;
    error?: TError;
    data?: TData;
}

export interface IQuery{
    limit?: number,
    order?: TypeOrder,
    [key: string]: unknown,
}

type TypeOrder = "desc" | 'asc';

const API_PREFIX = "https://api.thecatapi.com/v1";
const API_KEY = 'e995eaf5-e5f5-4cc7-b5af-780f24532746';

const  baseRequest =  <TMeta, TError, TData, TBody extends object>(
    url: string,
    method: string,
    body?: TBody
) => {
    return from(
        window.fetch(`${API_PREFIX}/${url}`, {
            method: method,
            body: body !== undefined ? JSON.stringify({...body}) : undefined,
            headers: {
                'Content-Type': 'application/json',
                'x-api-key': API_KEY
            }
        })
    ).pipe(
        switchMap((res: Response)=>{
            if(res.status === 200){
                return from(res.json()).pipe(
                    switchMap((parse)=>{
                        return of({ data: parse})
                    })
                )
            }
            return of({ error: res.statusText })
        }),
        catchError((e)=> of({ error: e.toString() }))
    )
}



export   const getPublicImage = ({query}:{query?: IQuery}): Observable<IResponse<{}, string, IImagesData[]>> =>{
      const q = query === undefined ? {limit: 10} : query;
      return baseRequest<{}, string, IImagesData[], any>(`images/search${parseQuery(q)}`,"GET" , undefined)
  }