import React from "react";
import "./container.styles.scss";

export const Container = ({children, fluid, paddingNull}: {children: JSX.Element | JSX.Element[], fluid?: boolean, paddingNull?: boolean})=>{
    return(<div className={`container${fluid ? "-fluid": ""} ${paddingNull ? "container_padding_null" : ""}`}>
        {children}
    </div>)
}