import { Action } from 'redux';
import {  ofType } from 'redux-observable';
import { switchMap, map, withLatestFrom } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { getPublicImage } from '../../../services/api';
import { toImagesAction } from './images.reducer';

export const setImagesEffect = (
    action$: Observable<Action>,
    state: Observable<any> //TODO: wrong type. Record not working
): Observable<Action> => {
    return action$.pipe(
        ofType(toImagesAction.imagesRequest.type),
        withLatestFrom(state),
        switchMap(([, state]) => {
            return getPublicImage({query: {limit: 20 }}).pipe(
                
                map((response) => {
                    if (response.error) {
                        return toImagesAction.imagesRequestFailed(
                            response?.error.toString()
                        );
                    }
                    if (response.data) {
                            return toImagesAction.imagesRequestSuccess(
                                response.data
                            );
                    }
                    return toImagesAction.imagesRequestFailed('Data is empty');
                })
            );
        })
    );
};