import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Loader } from "../../components/loader/loader.componet";
import { toImagesAction } from "../../store/slices/images/images.reducer";
import { toImagesSelector} from "../../store/slices/images/images.selector";
import { HomePage } from "./home.component";
import { CSSTransition } from 'react-transition-group';
import { IImagesFilter } from "../../store/slices/images/images.model";

export const HomePageContainer = () =>{
    
    const dispatch = useDispatch();
    const error = useSelector(toImagesSelector.error);
    const images = useSelector(toImagesSelector.images);
    const isLoading = useSelector(toImagesSelector.isLoading);
    const imagesLike = useSelector(toImagesSelector.imagesLike);
    const filter = useSelector(toImagesSelector.filter);
    
    const onChangeLike = (id: string | number) => dispatch(toImagesAction.toggleLike(id));
    const onRemoveImage = (id: string | number) => dispatch(toImagesAction.imageRemove(id));
    const onFilter = (filter: IImagesFilter) => dispatch(toImagesAction.imagesFilter(filter));

    const data = filter.like !== undefined && filter.like === true ? imagesLike : images;

    useEffect(()=>{
        if(images.length === 0) {
            dispatch(toImagesAction.imagesRequest());
        }
    }, []);

    return (
        <div>
            <CSSTransition
                in={!isLoading}
                timeout={300}
                classNames="fade"
            >
                <HomePage 
                    error={error} 
                    data={data} 
                    onChangeLike={onChangeLike}
                    onRemoveImage={onRemoveImage}
                    onFilter={onFilter}
                    filter={filter}
                />
            </CSSTransition>
            <CSSTransition
                in={isLoading}
                timeout={300}
                classNames="fade"
            >
                <Loader />
            </CSSTransition>
        </div>
    )
}