import React from "react";
import { IconFavorite } from "../../icons/favorite.icon";
import { IFavoriteButtonProps } from "./favorite.model";
import './favorite.scss';

export const FavoriteButton = ({className="", classNameSvg, active, onClick}: IFavoriteButtonProps)=>{
    return(
        <button 
            onClick={onClick}
            className={`button button-favorite ${className} ${active === true ? "button-favorite_active" : "" }`}
        >
            <IconFavorite className={classNameSvg}/>
        </button>
    )
}