import { createSlice } from '@reduxjs/toolkit';
import { IMAGES_KEY } from './images.const';
import { IImagesData, IImagesFilter, IImagesState } from './images.model';

const setImages = (state: IImagesState, { payload }: { payload: IImagesData[] }) => {
    state.images = payload;
};

const imagesRequest = (state: IImagesState) => {
    state.isLoading = true;
    state.error = '';
};

const imagesRequestSuccess = (
    state: IImagesState,
    { payload }: { payload: IImagesData[] }
) => {
        state.images = payload;
        state.isLoading = false;
        state.error = '';
    
};

const imagesRequestFailed = (
    state: IImagesState,
    { payload }: { payload: string }
) => {
    state.isLoading = false;
    state.error = payload;
};

const toggleLike = ( state: IImagesState,
    { payload }: { payload: string | number }) =>{
        const images =  [...state.images];
        const imgIndex = images.findIndex(x=> x.id === payload);
        if(imgIndex !== -1){
            const like = images[imgIndex].like;
            if(like){
                images[imgIndex].like = !like;
            } else {
                images[imgIndex].like = true;
            }
            
            state.images = [...images];
        } 

}

const imagesFilter = (state: IImagesState, {payload}:{payload: IImagesFilter})=>{
    state.filter = {...payload};
}

const imageRemove = (state: IImagesState,
    { payload }: { payload: string | number }) =>{
    const images =  [...state.images];
    state.images = images.filter(x=> x.id !== payload);
}

export const imagesSlice = createSlice({
    name: IMAGES_KEY,
    initialState: {
        isLoading: true,
        error: "",
        images: [],
        filter: {
            like: false
        }
    },
    reducers: {
        imagesRequestFailed,
        setImages,
        imagesRequest,
        imagesRequestSuccess,
        toggleLike,
        imageRemove,
        imagesFilter
    }
});

export const toImagesAction = imagesSlice.actions;