import { createSelector } from '@reduxjs/toolkit';
import { createFeatureSelector } from '../../../utils/store.utils';
import { IMAGES_KEY } from './images.const';
import { IImagesState} from './images.model';


export const favoriteSelector = createFeatureSelector<IImagesState>(IMAGES_KEY);

const error = createSelector(favoriteSelector, ({ error }) => error);
const isLoading = createSelector(favoriteSelector, ({ isLoading }) => isLoading);
const images  = createSelector(favoriteSelector, ({ images }) => images );
const imagesLike  = createSelector(favoriteSelector, ({ images }) => images.filter(x=> x.like !== undefined && x.like === true) );
const filter  = createSelector(favoriteSelector, ({ filter }) => filter );

export const toImagesSelector = {
    isLoading,
    images ,
    error,
    imagesLike,
    filter
};
